.home{
    background-color: rgb(44, 42, 41);
    min-height: 100vh;
}

.nav{
    width: 100%;
    height: 90px;
    background-color: rgb(29, 26, 29);
    display: flex;
    justify-content: center;
    padding-top: 10px;
  
}
.searchBar{
    margin: 10px;
    width: 50vw;
    height: 50px;
    border-radius: 30px;
    background-color: rgb(26, 24, 24);
    color: rgb(204, 194, 194);
    text-align: center;
}


   

.searchButton{
    margin: 10px 0 10px 1px;
    background-image: url('/src/images/131321833216315920414154-128.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgb(55, 49, 55);
  
}
.toLanding{
    background-image: url(/src/images/toHome.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 50%;
    margin: 10px;
    width: 50px;
    height: 50px;

}